/* Slideshow container */
.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

/* Caption text */
.text {
  color: #f7f4f4;
  font-size: 25px;
  font-weight: 100;
  padding: 10px 10px 10px 10px;
  position: absolute;
  bottom: 0%;
  width: 100%;
  text-align: center;
  margin-bottom: 25%;
}



/* The dots/bullets/indicators */
.dot {
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active {
  background-color: #ffffff;
}

/* Fading animation */
.fade {
  -webkit-animation-name: slide;
  -webkit-animation-duration: 2.5s;
  animation-name: slide;
  animation-duration: 2.5s;
}

@-webkit-keyframes slide {
  from {opacity: .4} 
  to {opacity: 1}
}

@keyframes slide {
  from {opacity: .4} 
  to {opacity: 1}
}

/* On smaller screens, decrease text size */
@media only screen and (max-width: 300px) {
  .text {font-size: 11px}
}

